import { BaseClass } from '../Classes/BaseClass'

export class BaseBuilder<Payload = any> {

  public value?: any

  public constructor(packet: Payload){
      this.defaults(packet)
  }

  public parse(): object { 
      return this.value ?? { ...this }
  }

  public defaults(defaults: any, context: any = this): any {
      for (const key in defaults) {
        const descriptor = Object.getOwnPropertyDescriptor(context, key)
        if(descriptor && (typeof descriptor.get === 'function' || !descriptor.configurable || !descriptor.writable)) continue
        if(typeof defaults[key] === 'object' || context[key] === undefined){
          context[key] = defaults[key]
        } else {
          if(BaseClass.isClass(defaults[key])){
            context[key] = defaults[key]
          } else {
            context[key] = this.defaults(defaults[key], context[key])
          }
        } 
      }
      return context
  }

}